.box1 {
  padding: 8px 62px 8px;
  box-shadow: 0 12px 30px rgba(0, 0, 0, .04);
  position: relative;
  overflow: hidden;
  min-height: 100%;
}

.box1 i {
  font-size: 40px;
  color: #262d36;
  padding-bottom: 10px;

}

.box1 img {
  opacity: 0;
  transition: .3s cubic-bezier(.53, .645, .51, 1.34);
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: hidden;
  z-index: 1;
  opacity: 0;
  will-change: transform;
  -webkit-transition-delay: .2s;
  -o-transition-delay: .2s;
  transition-delay: .2s;
  -webkit-transform: scale(1.15) translateY(-20px);
  -ms-transform: scale(1.15) translateY(-20px);
  transform: scale(1.15) translateY(-20px);
  min-height: 100%;
}

.box1:hover img {
  -webkit-transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  -o-transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  opacity: 1;
  -webkit-transform: translateY(20px) scale(1.15);
  -ms-transform: translateY(20px) scale(1.15);
  transform: translateY(20px) scale(1.15);
  min-height: 100%;
}

.txt {
  z-index: 5;
  position: relative;
}

.box1:before {

  -webkit-transition: .3s cubic-bezier(.53, .645, .51, 1.34);
  -o-transition: .3s cubic-bezier(.53, .645, .51, 1.34);
  transition: .3s cubic-bezier(.53, .645, .51, 1.34);
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: -100%;
  bottom: 0;
  z-index: 2;
  transition-delay: .2s;

}

.box1:hover:before {
  background-image: linear-gradient(to top, rgba(182, 129, 87, 0.9), rgba(68, 76, 92, 0.5));
  -webkit-transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  -o-transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  transition: .4s cubic-bezier(.01, .285, .31, 1.005);
  top: 0;
  transition-delay: .2s;

}

.box1:hover h4 {
  color: #fff;
}

.box1:hover i {
  color: #fff;
}

.box1:hover p {
  color: #fff;
}

